/**
    send Button

*/
.middle_place{
  position: relative;
  width: 20%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;

}
.sendBtn_div{
  height: auto;
  width: 100px;
  
}






.sendBtn {
	cursor: pointer;
	position: relative;
  height: 100px;
  width: 100px;
	border-radius: 50%;
	line-height: 2.5rem;
	font-size: 1.5rem;
	font-weight: 600;
  outline: none;
  text-align: center;
  
	
	border: 1px solid #000000;
	background-image: linear-gradient(-180deg, #dfdfdf 0%, #3a3a3d 100%);
	box-shadow: 0 1rem 1.25rem 0 rgba(46, 46, 46, 0.5),
							0 -0.25rem 1.5rem rgb(32, 32, 32) inset,
							0 0.75rem 0.5rem rgba(255, 255, 255, 0.808) inset,
							0 0.25rem 0.5rem 0 rgba(63, 63, 63, 0.678) inset;
  
              
}

.sendBtn_span{
  color: transparent;
  background-image: linear-gradient(0deg, #505050 0%, #FEFAFD 100%);
  -webkit-background-clip: text;
  background-clip: text;
  filter: drop-shadow(0 2px 2px rgb(3, 3, 3));
}



.sendBtn::before {
	content: "";
	display: block;
	height: 0.25rem;
	position: absolute;
	top: 0.5rem;
	left: 50%;
	transform: translateX(-50%);
	width: calc(100% - 7.5rem);
	background: #fff;
	border-radius: 100%;
	
	opacity: 0.7;
	background-image: linear-gradient(-270deg, rgba(255,255,255,0.00) 0%, #FFFFFF 20%, #FFFFFF 80%, rgba(255,255,255,0.00) 100%);
}

.sendBtn::after {
	content: "";
	display: block;
	height: 0.25rem;
	position: absolute;
	bottom: 0.75rem;
	left: 50%;
	transform: translateX(-50%);
	width: calc(100% - 7.5rem);
	background: #fff;
	border-radius: 100%;
	
	filter: blur(1px);
	opacity: 0.05;
	background-image: linear-gradient(-270deg, rgba(255,255,255,0.00) 0%, #FFFFFF 20%, #FFFFFF 80%, rgba(255,255,255,0.00) 100%);
}


