




.laodDialog{
    position: relative;
    height: 100vh;
    width: 100vW;
    display: flex;
    justify-content: center;
    align-items: center;

}

.loadDialog_mobile{
    height: 100vh;
    width: 100vW;
    display: flex;
    justify-content: center;
    align-items: center;


}


.btn_back_dialog{
    position: absolute;
    height: 100px;
    width: 100px;
    left: 0;
    top: 0;
    border: none;
    outline: none;
    background: none;
}

.btn_back_dialog:hover{
    cursor: pointer;
}




.dialog_res_div{
    width: 100%;
    height: 100%;
}

.dialog_res_div_2{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dialog_res_btn{
    border: none;
    outline: none;
    background: none;
    
    display: flex;
    justify-content: center;
    align-items: center;
    
    

}
.dialog_res_img{
    max-height: 90vh;
    max-width: 90vh;
    width: auto;
    height: auto;


}

.dialog_res_img_mobile{
    max-height: 70vh;
    max-width: 80vh;
    width: auto;
    height: auto;


}

.dialog_res_img:hover{

    cursor: pointer;
    border: 2px solid rgb(0, 0, 0);

}
