




.checkDialog{
    position: relative;
    margin: 15% 25%;

}

.checkDialog_mobile{
    position: relative;
    margin: 35% 15%;

}


.dialogCheckFrame{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    vertical-align: middle;
    
    

}

.dialogCheckText{
    position: relative;
    width: 100%;
    text-align: center;
    font-size: 1.1em;
}

.dialogCheckBtnDiv{
    position: relative;
    width: 100%;
    text-align: center;

}

.dialogCheckBtn{
    width: 40%;
    height: 60px;
    font-size: 1.5em;
    outline: none;
    border-radius: 10px;
    background: rgb(99, 156, 221, 0.5);
    color: white;
    margin-left: 5px;
    margin-right: 5px;
    border: none;

}
.dialogCheckBtn:hover{
    background: rgb(99, 156, 221);
    cursor: pointer;

}