

.topbar_1{
  position: relative;
  height: 5vh;
  width: 100vw;
  background: whitesmoke;
  display: flex;
  flex-direction: row;
}
.topbar_left{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: auto;
  margin-left: 5px;
  margin-right: 5px;
}
.topbar_right{
  display: flex;
  justify-content: center;
  
  position: relative;
  width: 100%;
  height: 100%;
  

}

.topbar_appName{
  align-items: center;
  width: auto;
  display: flex;
  color: rgb(54, 54, 54);
  font-size: 25px;
  font-weight: bold;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;

}













.connection_dot_green{
  width: 5px;
  height: 5px;
  margin-left: 5px;
  border-radius: 50%;
  background: green;
}



.connection_dot_red{
  width: 5px;
  height: 5px;
  margin-left: 5px;
  border-radius: 50%;
  background: rgb(170, 25, 38);
  
}